.navbar {
  display: block;
  position: absolute;
  top: 0;
  margin: 0;
  padding: 45px 0;
  z-index: 1;
  width: 100%;
}

.navbar_list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin: 0 auto;
}
.navbar_list_item {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
}
.navbar_list_item:first-child {
  padding-left: 0;
}
.navbar_list_item__logo {
  height: 60px;
  width: 60px;
  margin-right: 56px;
}
.navbar_list_link {
  font-size: 20px;
  line-height: 50px;
  color: rgb(120, 120, 120);
  text-decoration: none;
  white-space: nowrap;
}
.navbar_list_link:focus {
  outline: none;
}
[data-current="about"] .navbar_list_item__about .navbar_list_link,
.navbar_list_item__current .navbar_list_link {
  color: var(--color-secondary--darken);
}

[data-current="about"] .navbar_list_item__about::after,
.navbar_list_item__current::after,
.navbar_list_item:focus-within::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -2px;
  width: calc(100% - 30px);
  height: 4px;
  border-radius: 20px;
  background-color: #f0d8ce;
}
.navbar_list_item:focus-within::after {
  opacity: 0.5;
}
.navbar_list_item__current::after {
  opacity: 1 !important;
}
.navbar_hamburger {
  display: none;
  border: 0;
  box-shadow: none;
  width: 35px;
  height: 25px;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 34px;
  z-index: 1;
  background-color: transparent;
  background-image: url("../assets/images/hamburger.svg");
}
.navbar_close-arrow {
  display: none;
}

@media (max-width: 768px) {
  .navbar {
    width: 100%;
    background-color: white;
    box-shadow: var(--big-shadow);
    transform: translateX(-100%);
    opacity: 0;
    transition: all 0.2s ease-in-out;
  }
  .navbar.open {
    transform: translateX(0);
    opacity: 1;
    transition: all 0.3s ease-in-out;
  }
  .navbar.open .container {
    z-index: 2;
  }
  .navbar_hamburger {
    display: block;
  }
  .navbar_close-arrow {
    display: block;
    padding: 0;
    margin: 0px;
    box-sizing: border-box;
    background-color: transparent;
    position: absolute;
    top: 46px;
    left: 20px;
    height: 13.5px;
    width: 18px;
    background-image: url("../assets/images/icon-arrow.svg");
    background-repeat: no-repeat;
    background-size: contain;
    box-shadow: none;
    border: 0;
    transform: rotate(180deg);
  }
  .navbar_list {
    flex-direction: column;
    align-items: center;
  }
  .navbar_list_item__logo {
    margin-right: 0;
    margin-bottom: 30px;
  }
  .navbar_list_item {
    margin-bottom: 20px;
  }
}
@media (max-width: 1024px) {
  .navbar_hamburger {
    left: 62px;
  }
}
@media (max-width: 425px) {
  .navbar_hamburger {
    left: 20px;
  }
}
